import React, { useState } from 'react';
import css from './Pagination.module.css';

const Pagination = ({totalCount, pageSize, currentPage, onPageSelect, portionSize = 10}) => {
  const pagesCount = Math.ceil(totalCount / pageSize);
  // const portionsCount = Math.ceil(pagesCount / portionSize);

  const [currentPortion, setCurrentPortion] = useState(1);

  const getNextPortion = () => {
    setCurrentPortion(currentPortion + 1);
  };

  const getPreviousPortion = () => {
    setCurrentPortion(currentPortion - 1);
  };

  const leftBorder = currentPortion * portionSize - portionSize + 1;
  let rightBorder = currentPortion * portionSize;
  const pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    if(i < leftBorder || i > rightBorder) {
      continue;
    }

    pages.push(
      <span
        key={i}
        className={currentPage === i ? css.selectedPage : ''}
        onClick={() => onPageSelect(i)}
      >
        {i}
      </span>);
  }

  return (
    <div style={{display: 'flex'}}>
      {leftBorder > 1 && <button onClick={getPreviousPortion}>&#60;</button>}
      <div>
        {pages}
      </div>
      {rightBorder < pagesCount && <button onClick={getNextPortion}>&#62;</button>}
    </div>
  );
};

export default Pagination;