import React from 'react';
import { follow, unfollow, setCurrentPage, toggleFollowingProgress, requestUsers } from '../../redux/users-reducer';
import { connect } from 'react-redux';
import UsersPage from './UsersPage';
import Preloader from '../common/Preloader/Preloader';
import { compose } from 'redux';
import { getPageSize, getTotaUserslCount, getCurrentPage, getIsFetching, getFollowingInProgress, getUsers } from '../../redux/users-selectors';


class UsersContainer extends React.Component {

  componentDidMount() {
    this.getUsers();
  }

  getUsers = (pageNumber) => {
    const { requestUsers, currentPage, pageSize } = this.props;
    const page = pageNumber || currentPage;
    requestUsers(page, pageSize);
  }

  handlePageSelection = (pageNumber) => {
    this.getUsers(pageNumber);
  } 

  render() {
    const { 
      users, 
      totalCount, 
      pageSize, 
      currentPage,
      isFetching,
      followingInProgress,
      follow,
      unfollow
    } = this.props;

    return (
      <>
        { isFetching ? <Preloader /> : null}
        <UsersPage 
          totalCount={totalCount}
          pageSize={pageSize}
          users={users}
          currentPage={currentPage}
          followingInProgress={followingInProgress}
          handlePageSelection={this.handlePageSelection}
          follow={follow}
          unfollow={unfollow}
        />
      </>
    );
  }
}

let mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    totalCount: getTotaUserslCount(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    followingInProgress: getFollowingInProgress(state)
  };
};

let mapDispatchToPropsObj = {
  follow,
  unfollow,
  setCurrentPage,  
  toggleFollowingProgress,
  requestUsers
}

export default compose(
  connect(mapStateToProps, mapDispatchToPropsObj)
)(UsersContainer);